<template>
<div class="main">
  <div class="about">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
    
  </div>
  <BottomNav ref="bottomNav" @on-pause="pauseAnimation" @on-skip="skipAnimation"></BottomNav>
</div>
  
</template>

<script>

import StyleEditor from '../components/StyleEditor';
import ResumeEditor from '../components/ResumeEditor';
import BottomNav from '../components/buttomNav'
import fullStyle from '../assets/style.js';
import fullMarkdown from '../assets/my.js';
import { Promise, reject, async } from 'q';



export default {
  data() {
    return {
        interval:0,
        currentStyle:'',
        fullStyle:fullStyle, 
        currentMarkdown:'',
        fullMarkdown:fullMarkdown,
        enableHtml:false,
        timer:null,
    }
  },
  created(){
    this.makeResume();
  },

  computed: {

  },
  methods: {
      //pause animation
      pauseAnimation(bool){
        if(bool && this.timer){
          clearTimeout(this.timer);
        }else{
          this.makeResume();
        }
      },
      //skip animation
      skipAnimation(){
        if(this.timer){
					clearTimeout(this.timer);
				}
				let str = '';
				this.fullStyle.map((f) => {
					str += f;
				})
				setTimeout(() => {
					this.$set(this.currentStyle,'code',str);
				},100)
				this.currentMarkdown = my;
                this.enableHtml = true;
                this.$refs.styleEditor.goBottom();
      },
      //加载动画
      makeResume: async function(){
          await this.writeShowStyle(0)
          await this.writeShowResume()
          await this.writeShowStyle(1)
          await this.showHtml()
          await this.writeShowStyle(2)
      },
        // 写入css代码
        writeShowStyle(n) {
        return new Promise((resolve, reject) => {
            let interval = this.interval
            let showStyle = (async function () {
                let style = this.fullStyle[n]
                if (!style) { return }
                // 计算前 n 个 style 的字符总数
                let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
                let prefixLength = length - style.length
                if (this.currentStyle.length < length) {
                let l = this.currentStyle.length - prefixLength
                let char = style.substring(l, l + 1) || ' '
                this.currentStyle += char
                if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                    this.$nextTick(() => {
                    this.$refs.styleEditor.goBottom()
                    })
                }
                this.timer=setTimeout(showStyle, interval)
                } else {
                resolve()
                }
            }).bind(this)
            showStyle()
        })
      },
    //写入简历
    writeShowResume(){
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              this.timer=setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
    },
    // markdown转html
     showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          resolve()
        })
      },
  },
  components: {
      StyleEditor,
      ResumeEditor,
      BottomNav,
  }
}
</script>

