<template>
	<div id="bottom">
		<a  id="pause" @click="pauseFun">{{ !paused ? '暂停动画' : '继续动画 ||' }}</a>
		<a  id="skipAnimation" @click="skipAnimationFun">跳过动画</a>
		<p>
			<span v-for="(url,index) in demourl" :key="index">
				<a :href="url.url">{{ url.title }}</a>
			</span>
		</p>
	</div>
</template>
<script>
	export default{
		name:'bottom',
		data(){
			return{
				demourl:[
					{url:'',title:'个人网站'},
					{url:'',title:'github'}
				],
				paused:false,//暂停
			}
		},
		mounted(){
			
		},
		methods:{
			// 跳过动画
			skipAnimationFun(e){
				e.preventDefault();
				this.$emit('on-skip');
			},
			// 暂停动画
			pauseFun(e){
				e.preventDefault();
				this.paused = !this.paused;
				this.$emit('on-pause',this.paused);
			},
		}
	}
</script>
<style scoped>
	#bottom{
		position:fixed;
		bottom:5px;
		left:0;
		right:0;
	}
	#bottom p{
		float: right;
	}
	#bottom a{
		text-decoration: none;
		color: #999;
		cursor:pointer;
		margin-left:5px;
	}
	#bottom a:hover,#bottom a:active{
		color: #010a11;
	}
</style>